.acf-map {
width: 100%;
height: 400px;
border: #ccc solid 1px;
margin: 20px 0;

  .gm-ui-hover-effect {
    position: absolute !important;
    top: 0;
    right: 0;
  }
  
  .gm-style-iw-d h3 {
    margin-top: 0;
  }
}

/* fixes potential theme css conflict */
.acf-map img {
 max-width: inherit !important;
}

.location__map {
  width: 100%;
  height: 400px;
  border: #ccc solid 1px;
  margin: 20px 0;
}

.location__map img {
 max-width: inherit !important;
}